.wrapper {
  position: relative;
  background: #ffffff;
  box-shadow: 0px 2px 4px rgba(13, 11, 12, 0.2);
  border-radius: 6px;
  overflow: auto;
  max-height: 40px;
  z-index: 5;
}

.dropdownIcon {
  position: absolute;
  top: 8px;
  right: 3px;
  cursor: pointer;
}

.option {
  height: 40px;
  width: 100%;
  padding: 8px 5px 9px 16px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
}

.option.inList[data-selected="true"] {
  background: #ff1d6c;
  color: white;
  cursor: default;
}

.option.inList:not([data-selected="true"]):hover {
  background: #ddd;
}

.optionText {
  width: 64px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  display: flex;
  justify-content: space-between;
  font-size: 16px;
}
